@import url(https://fonts.googleapis.com/css?family=Acme&display=swap);
html {
  height: 100%;
  width: 100%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: linear-gradient(-30deg, #000 0%, #d754ad 10%, #f96785 62%, #fe7333 100%); */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.song-search-wrapper {
  font-family: Arial, Helvetica, sans-serif; }
  .song-search-wrapper .form-wrapper {
    padding: 20px; }
    .song-search-wrapper .form-wrapper .input-wrapper {
      width: 100%; }
      .song-search-wrapper .form-wrapper .input-wrapper .enter-artist {
        margin-right: 10px; }
      .song-search-wrapper .form-wrapper .input-wrapper .submit {
        line-height: 1.17648;
        min-width: 28px;
        padding: 8px 16px;
        border-radius: 18px;
        background: #000;
        color: white;
        margin-left: 10px; }
    .song-search-wrapper .form-wrapper .banner-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 0 auto;
      width: 450px; }
      .song-search-wrapper .form-wrapper .banner-wrapper img {
        width: 125px; }
      .song-search-wrapper .form-wrapper .banner-wrapper .itunes-heading {
        font-size: 64px;
        line-height: 1.0625;
        font-weight: 600;
        letter-spacing: -.009em;
        padding: 10px;
        font-family: "SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif; }
      .song-search-wrapper .form-wrapper .banner-wrapper .itune-description {
        font-size: 24px;
        line-height: 1.16667;
        font-weight: 600;
        letter-spacing: .009em;
        padding: 20px;
        font-family: "SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif; }
  .song-search-wrapper .card-wrapper {
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    text-align: center; }
    .song-search-wrapper .card-wrapper .item-wrapper {
      width: 20%;
      height: 100%;
      padding: 10px; }
      .song-search-wrapper .card-wrapper .item-wrapper .inner-wrapper {
        text-align: center;
        align-items: center;
        padding: 10px;
        margin-bottom: 5px;
        box-shadow: 12px 15px 35px -19px black; }
        .song-search-wrapper .card-wrapper .item-wrapper .inner-wrapper .image-wrapper img {
          width: 100px;
          height: 100px; }
        .song-search-wrapper .card-wrapper .item-wrapper .inner-wrapper .song-name {
          font-size: 12px;
          padding: 5px 0 0 0;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
          height: 55px; }
        .song-search-wrapper .card-wrapper .item-wrapper .inner-wrapper .artist-name {
          font-size: 10px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
          height: 40px; }
        .song-search-wrapper .card-wrapper .item-wrapper .inner-wrapper .preview-button {
          font-size: 10px;
          color: white;
          background-color: black;
          border-color: black;
          padding: 4px 8px;
          border-radius: 18px;
          background: #000;
          color: white; }

@media only screen and (max-width: 768px) {
  .song-search-wrapper .form-wrapper .input-wrapper {
    display: flex; }
    .song-search-wrapper .form-wrapper .input-wrapper .enter-artist {
      width: 40%; }
    .song-search-wrapper .form-wrapper .input-wrapper .enter-number {
      width: 30%; }
    .song-search-wrapper .form-wrapper .input-wrapper .submit {
      width: 30%;
      padding: 6px 14px; }
  .song-search-wrapper .form-wrapper .banner-wrapper {
    width: 250px; }
    .song-search-wrapper .form-wrapper .banner-wrapper .itunes-heading {
      font-size: 30px; }
    .song-search-wrapper .form-wrapper .banner-wrapper .itune-description {
      font-size: 15px; }
  .song-search-wrapper .card-wrapper .item-wrapper {
    width: 25%; } }

@media only screen and (max-width: 600px) {
  .song-search-wrapper .card-wrapper .item-wrapper {
    width: 50%; } }

